import { combineReducers } from 'redux'
import { handleActions, combineActions } from 'redux-actions'

import { boolReducer } from '@common/helpers'
import { actions } from './actions'

export const initialState = {
  list: null
}

const isListLoading = boolReducer(
  [combineActions(actions.getListRequest)],
  [combineActions(actions.getListSuccess, actions.getListFail)]
)

const data = handleActions(
  {
    [actions.getListSuccess]: (state, { payload }) => ({
      ...state,
      list: payload
    })
  },
  initialState
)

export const reducer = combineReducers({
  data,
  isListLoading
})

export const appSelectors = {
  list: state => (state.organizations ? state.organizations.data.list : []),
  isListLoading: state => state.organizations.isListLoading
}
