import { createActions } from 'redux-actions'

import { createRequestAction, getResponseError } from '@common/helpers'
import { ENDPOINTS, ACTION_TYPES } from './constants'

const actionCreators = createActions({
  ...createRequestAction(ACTION_TYPES.GET_LIST)
})

export function getList() {
  return async (dispatch, getState, { api }) => {
    dispatch(actionCreators.getListRequest())
    try {
      const { data } = await api.get(ENDPOINTS.LIST)
      dispatch(actionCreators.getListSuccess(data))
      return data
    } catch (e) {
      const error = getResponseError(e)
      dispatch(actionCreators.getListFail(error))
      return Promise.reject(error)
    }
  }
}

export const actions = {
  ...actionCreators,
  getList
}
