import React from 'react'
import PropTypes from 'prop-types'

// Mui
import Paper from '@material-ui/core/Paper'
import Typography from '@material-ui/core/Typography'
import IconButton from '@material-ui/core/IconButton'
import MoreIcon from '@material-ui/icons/MoreHoriz'
import Avatar from '@material-ui/core/Avatar'

import { useStyles } from './styles'

function Card(props) {
  const classes = useStyles()
  const { data } = props
  return (
    <Paper className={classes.root}>
      <div className={classes.row}>
        <Typography className={classes.name}>{data.name}</Typography>
        <div className={classes.grow} />
        <IconButton>
          <MoreIcon />
        </IconButton>
      </div>
      <div className={classes.row}>
        <Typography className={classes.label}>Инн: </Typography>
        <Typography className={classes.value}>{data.nn}</Typography>
      </div>
      <div className={classes.row}>
        <Typography className={classes.label}>Адрес: </Typography>
        <Typography className={classes.value}>{data.address}</Typography>
      </div>
      <div className={classes.row}>
        <Typography className={classes.label}>Телефон: </Typography>
        <Typography className={classes.value}>{data.phone}</Typography>
      </div>
      <div className={classes.row}>
        <Typography className={classes.label}>Сайт: </Typography>
        <Typography className={classes.value}>{data.site}</Typography>
      </div>
      <div className={classes.row}>
        <Typography className={classes.label}>Эл почта: </Typography>
        <Typography className={classes.value}>{data.mail}</Typography>
      </div>
      <div className={classes.avatars}>
        <Avatar>H</Avatar>
        <Avatar className={classes.orange}>N</Avatar>
        <Avatar className={classes.purple}>OP</Avatar>
      </div>
    </Paper>
  )
}

Card.propTypes = {
  data: PropTypes.object
}

export default Card
