import React from 'react'
import { Redirect } from 'react-router-dom'
import { useSelector } from 'react-redux'
import { useInjectReducer } from '@utils/injectReducer'

import { authSelectors } from '@services/auth'
import { reducer } from '@states/Organizations'

import OrganizationsContainer from '@containers/Organizations'

const key = 'organizations'

function OrganizationsPage() {
  useInjectReducer({ key, reducer })

  const isAuth = useSelector(authSelectors.getAuthStatus)

  if (!isAuth) return <Redirect to="/signin" />

  return (
    <OrganizationsContainer />
  )
}

export default OrganizationsPage
